@import url("variables");
.about{
  background-color: #f8fafa;

  .about__title{
    padding-top: 70px;
    font-family: @Montserrat;
    color: rgba(0, 0, 0, 0.9);
    letter-spacing: 1.28px;
    font-size: 23px;
    margin-top: 0;
  }
  .about__text{
    font-family:@Merriweather;
    color: rgba(0, 0, 0, 0.62);
    font-size: 14px;
    margin-bottom: 50px;
    margin-left: 5%;
    margin-right: 5%;
  }
  .about__wrapper{
    .about-wrapper{
      .about-wrapper__photo{
        width: 100%;
      }
      .about-wrapper__title{
        font-family: @Merriweather;
        font-weight: 700;
        margin-top: 50px;
        font-size: 19px;
        margin-left: 5%;
        margin-right: 5%;
      }
      .about-wrapper__title--two{
        font-family: @Merriweather;
        font-weight: 700;
        margin-top: 30px;
        font-size: 19px;
        margin-left: 5%;
        margin-right: 5%;
      }
      .about-wrapper__text{
        font-size: 14px;
        margin-left: 5%;
        margin-right: 5%;
        font-family: @Raleway;
        margin-top: 30px;
        color: rgba(0, 0, 0, 0.74);
        line-height: 1.55;
      }
      .about-wrapper__text--two{
        font-size: 14px;
        margin-left: 5%;
        margin-right: 5%;
        font-family: @Raleway;
        margin-top: 20px;
        color: rgba(0, 0, 0, 0.74);
        line-height: 1.55;
      }
    }
    .about-wrapper-two{
    }
  }
}
@media (min-width:@tablet-width) {
  .about{
    .about__title{
      font-size: 28px;
      margin-bottom: 0;
      padding-top: 100px;
    }
    .about__text{
      margin-top: 5px;
    }
    .about__wrapper{
      display: flex;

      .about-wrapper{
        width: 50%;
        text-align: left;

        .about-wrapper__photo{
          max-width: 500px;
        }
        .about-wrapper__title{
          margin-top: 20px;
          margin-bottom: 15px;
        }
        .about-wrapper__text{
          margin-top: 10px;
        }
      }
    }
  }
}
@media (min-width:@desktop-width) {

  .about{
    .about__title{
      font-size: 33px;
      padding-top: 120px;
    }
    .about__text{
      font-size: 15px;
      margin-bottom: 80px;
    }
    .about__wrapper{
      .about-wrapper{
        width: 44%;
        text-align: left;
        padding-left: 6%;
        padding-right: 10%;

        .about-wrapper__photo{
          margin-left: 100px;
        }
      }
      .about-wrapper-two{
        margin-right: 35px;
      }
    }
  }
}
@media (min-width:@desktop-two-width) {
  .about{
    .about__title{
      font-size: 38px;
      line-height: 1.17;
      letter-spacing: 0px;
      font-weight: 800;
      margin-left: 26px;
    }
    .about__text{
      font-size: 18px;
      letter-spacing: 1.3px;
      line-height: 1.4;
    }
    .about__wrapper{
      margin-top: 117px;

      .about-wrapper{
        padding-left: 106px;

        .about-wrapper__photo{
          max-width: 440px;
        }
      }
      .about-wrapper-two{
        margin-right: 35px;
        margin-left: -111px;
        margin-top: 5px;

        .about-wrapper__title{
          font-size: 20px;
          margin-bottom: 21px;
          letter-spacing: 1.2px;
        }
        .about-wrapper__title--two{
          margin-bottom: 15px;
        }
        .about-wrapper__text{
          font-size: 16px;
          letter-spacing: 0.8px;
          line-height: 1.75;
          margin-right: 0px;
        }
        .about-wrapper__text--two{
          font-size: 16px;
          margin-top: 28px;
          line-height: 1.75;
          letter-spacing: 0.8px;
        }
      }
    }
  }
}
