@import url("variables");

.form{
  background-image: url("../../build/img/christmas_snow_trees-wide.jpg");
  background-position-y: 1041px;
  padding-top: 1px;
  background-position-x: -199px;

  background-blend-mode: lighten;

  input[type="text"]::-webkit-input-placeholder,input[type="email"]::-webkit-input-placeholder,textarea[name="comment"]::-webkit-input-placeholder{
    color: rgba(255, 255, 255, 0.6);
  }
  .form-wrapper{
    padding-left: 5%;
    padding-right: 5%;
    text-align: left;
    margin-top: 70px;
    box-shadow: 0 0 30px 0 rgba(21, 21, 21, 0.12);
    background-color: rgba(2, 22, 35, 0.71);
    padding-top: 20px;
  }

  .form__item{
    margin-bottom: 15px;
    outline: 0;
    padding-right: 0px;
    border: none;
    border-bottom: 1px solid rgba(255,255,255,0.2);
    width: 99%;
    background-color: transparent;
    font-family: @Raleway,sans-serif;
    padding-bottom: 20px;
    color: rgba(255,255,255,.6);
    font-size: 17px;
  }


  .form__item--first{
    padding-top: 20px;
    text-transform: capitalize;
  }
  .form__comment{
    background: 0 0;
    outline: 0;
    border: none;
    font-family: @Raleway,sans-serif;
    color: rgba(255,255,255,.6);
    width: 99%;
    border-bottom: 1px solid rgba(255,255,255,.2);
    margin-bottom: 30px;
    resize:none;
    font-size: 17px;
    padding-bottom: 20px;
  }
  .form__button{
    background-color:rgba(110, 238, 228, 1);
    font-family: @MontserratRegular;
    color: rgba(0, 0, 0, 0.7);
    border-radius: 2px;
    outline: none;
    border: none;
    width: 65%;
    height: 40px;
    letter-spacing: 2.16px;
    font-size: 17px;
    margin-bottom: 30px;
    transition: all linear 0.25s;
    &:hover{
      transition-duration: 0.25s;
      border-radius: 40px;
    }
  }
}
@media (min-width:@tablet-width) {
  .form{
    width: 46%;
    padding-right: 2%;
    padding-left: 3%;
    padding-top: 0;
    background-image: none;
    background-color: rgba(2,22,35,0);

    .form-wrapper{

      .form__item{
        font-size: 15px;
      }
      .form__comment{
        font-size: 15px;
      }
      .form__button{
        font-size: 18px;
      }
    }
  }
}

@media (min-width:@desktop-width) {
  .form{
    width: 50%;

    .form-wrapper{

      .form__item{
        padding-bottom: 13px;
        margin-bottom: 26px;
        width: 101%;

      }
      .form__comment{
        width: 101%;
        padding-bottom: 18px;
        margin-bottom:34px;
      }

      .form__button{
        width: 45%;
        height: 53px;
        margin-bottom: 37px;

      }
    }
  }
}
@media (min-width:@desktop-two-width) {
  .form{
    width: 614px;
    padding-left: 149px;
    padding-right: 25px;

    .form-wrapper{
      padding-top: 24px;

      .form__item{
        width: 99%;
      }
      .form__button{
        width: 44%;
        font-weight: 700;
      }
    }
  }
}
