@import url("variables");

.contacts{
  background-image: url("../../build/img/christmas_snow_trees-wide.jpg");
  background-position-y: 533px;
  background-position-x: -199px;

  background-blend-mode: lighten;

  .contacts-wrapper{
    background-color: rgba(2,22,35,.71);
    color: rgba(255,255,255,.6);
    text-align: left;
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 17px;

  }
  .contacts__adress{
    font-family: @Raleway;
    font-size: 13px;
    margin-bottom: 10px;
    font-size: 17px;
  }
  .contacts__adress--last{
    margin-bottom: 40px;
  }
  .contacts__adress--indent{
    margin-bottom: 30px;
  }
  .contacts__map{
    margin-bottom: 5%;
    width: 100%;
    height: 250px;
  }
}

@media (min-width:@tablet-width) {

  .contacts{
    width: 46%;
    padding-right: 3%;
    padding-top:70px;
    background-image: none;
    background-color: transparent;
  }
  .contacts-wrapper{
    .contacts__adress{
      margin-top: 10px;
      font-size: 15px;
    }
    .contacts__adress--last{
      margin-bottom: 30px;
    }
    .contacts__map{
      height:auto;
      margin-bottom: 32px;
    }
  }
}

@media (min-width:@desktop-width) {
  .contacts{
    width: 42%;

    .contacts-wrapper{
      padding-bottom: 12px;
      padding-left: 34px;
      padding-top: 28px;

      .contacts__adress{
        margin-top: 0px;
        font-size: 15px;
        margin-bottom: 6px;
      }
      .contacts__adress--last{
        padding-bottom: 24px;
      }
      .contacts__map{
        height: 222px;
        margin-top: 18px;
        margin-left: -9px;
        margin-bottom: 3px;
      }
    }
  }
}
@media (min-width:@desktop-two-width) {
  .contacts{
    width: 501px;
    .contacts-wrapper{
      padding-left: 32px;
      padding-top: 32px;

      .contacts__map{
        height: 222px;
        width: 102%;
        margin-top: 18px;
        margin-left: -8px;
        margin-bottom: 3px;
      }
    }
  }
}
