@import url("variables");

.team{
  margin-top: 70px;

  .team__title{
    font-family: @Montserrat;
    font-size: 23px;
    color: rgba(0, 0, 0, 0.9);
    letter-spacing: 1.28px;
  }
  .team__text{
    font-family:@Merriweather;
    color: rgba(0, 0, 0, 0.62);
    font-size: 14px;
    margin-bottom: 50px;
    margin-left: 5%;
    margin-right: 5%;
  }
  .team__flex{
    display: flex;
    flex-direction: column;

    .team__photo{
      width: 90%;
      margin-top: 5%;
      margin-left: 5%;
      margin-right: 5%;
      position: relative;
    }
  }
}
@media (min-width:@tablet-width) {
  .team{

    .team__title{
      font-size: 28px;
      margin-bottom: 0;
      margin-top: 100px;
    }
    .team__text{
      margin-top: 5px;
    }
    .team__flex{
      flex-wrap: wrap;
      justify-content: center;
      flex-direction: row;

      .team__photo{
        width: 30%;
        margin: 1% 1%;
      }
    }
  }
}
@media (min-width:@desktop-width) {

  .team{

    .team__title{
      font-size: 33px;
      padding-top: 70px;
    }
    .team__text{
      font-size: 15px;
      margin-bottom: 80px;
    }
    .team__flex{
      margin-bottom: 150px;

      .team__photo{
        width: 25%;
        margin: 7px;
      }
    }
  }
}

@media (min-width:@desktop-two-width) {
  .team{
    margin-top: 131px;

    .team__title{
      font-size: 38px;
      font-weight: 800;
      margin-left: 0px;
      letter-spacing: 0px;
    }
    .team__text{
      font-size: 17px;
      letter-spacing: 1.2px;
      line-height: 0.6;
    }
    .team__flex{
      margin-top: 118px;

      .team__photo{
        width: 26%;
        margin: 6px;
      }
    }

  }
}
