body {
  margin: 0 auto;
}
body .header {
  background-color: #eff0f2;
}
body .header__logo {
  font-family: "Montserrat-Bold", sans-serif;
  font-weight: 800;
  text-align: center;
  margin: 0;
  font-size: 20px;
  display: flex;
  transition: all linear 0.25s;
}
body .header__logo .header__logo--link {
  text-decoration: none;
  color: inherit;
  border: 5.5px solid #170f1c;
  display: inline-block;
  width: 100%;
}
body .header__logo .header__logo--link:hover {
  background-color: rgba(118, 244, 234, 0.5);
  border: 5.5px solid black;
  transition-duration: 0.25s;
  border-radius: 40px;
}
body .header__logo .header__logo--symbol {
  font-size: 26px;
  color: #77efe5;
}
body .header__intro {
  text-align: center;
  font-size: 22px;
  font-family: "Montserrat-Bold", sans-serif;
  margin-top: 90px;
  text-shadow: 3.5px 8.3px 10px rgba(0, 0, 0, 0.24);
  font-weight: 800;
  color: #505050;
}
body .header__intro .header__intro--back {
  background-color: rgba(118, 244, 234, 0.5);
  padding-left: 2px;
  padding-right: 2px;
  margin-left: -5px;
  margin-right: -5px;
}
body .header__intro .header__intro--back-two {
  background-color: rgba(118, 244, 234, 0.5);
}
body .header__button {
  margin-left: 15%;
  margin-right: 15%;
  width: 70%;
  border: 2px solid rgba(0, 0, 0, 0.5);
  margin-top: 50px;
  font-family: "Montserrat-Regular", sans-serif;
  font-weight: normal;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.75);
  padding-top: 10px;
  padding-bottom: 10px;
  height: auto;
  margin-bottom: 70px;
  transition: all linear 0.25s;
}
body .header__button:hover {
  background-color: rgba(118, 244, 234, 0.5);
  border: 2px solid #000;
  -webkit-transition-duration: 0.25s;
  transition-duration: 0.25s;
  border-radius: 40px;
}
body .header__image {
  display: none;
}
