@import url("variables");
.main{
  text-align: center;
  .display-none{
    display: none;
  }
}
@media (min-width:768px) {
  .main{
    .wrapper-form-contacts{
      display: flex;
      background-image: url("../../build/img/christmas_snow_trees-wide.jpg");
      background-position-y: 967px;
      padding-top: 1px;
      padding-bottom: 80px;
      margin-top: 70px;
      background-color: rgba(255,255,255,0.4);
      background-blend-mode: lighten;
    }
  }
}
@media (min-width:1200px) {
  .main{
    .wrapper-form-contacts{
      padding-bottom: 122px;
    }
  }
}
@media (min-width:1440px) {
  .main{
    .wrapper-form-contacts{
      margin-top: 225px;
    }
  }
}
