@import "variables";
body{
  margin: 0 auto;

  .header{
    background-color: #eff0f2;
  }
  .header__logo{
    font-family:@Montserrat;
    font-weight: 800;
    text-align: center;
    margin: 0;
    font-size: 20px;
    display: flex;
    transition: all linear 0.25s;

    .header__logo--link{
      text-decoration: none;
      color: inherit;
      border: 5.5px solid #170f1c;
      display: inline-block;
      width: 100%;
      &:hover{
        background-color: rgba(118,244,234,.5);
        border: 5.5px solid black;
        transition-duration: 0.25s;
        border-radius: 40px;
      }
    }
    .header__logo--symbol{
      font-size: 26px;
      color: #77efe5;
    }

  }
  .header__intro{
    text-align: center;
    font-size: 22px;
    font-family: @Montserrat;
    margin-top: 90px;
    text-shadow: 3.5px 8.3px 10px rgba(0, 0, 0, 0.24);
    font-weight: 800;
    color: #505050;

    .header__intro--back{
      background-color: rgba(118, 244, 234, 0.5);
      padding-left: 2px;
      padding-right: 2px;
      margin-left: -5px;
      margin-right: -5px;

    }
    .header__intro--back-two{
      background-color: rgba(118, 244, 234, 0.5);
    }
  }
  .header__button{
    margin-left: 15%;
    margin-right: 15%;
    width: 70%;
    border: 2px solid rgba(0, 0, 0, 0.5);
    margin-top: 50px;
    font-family: "Montserrat-Regular", sans-serif;
    font-weight: normal;
    font-size: 16px;
    color: rgba(0,0,0,0.75);
    padding-top: 10px;
    padding-bottom: 10px;
    height: auto;
    margin-bottom: 70px;
    transition: all linear 0.25s;

    &:hover{
      background-color: rgba(118,244,234,.5);
      border: 2px solid #000;
      -webkit-transition-duration: .25s;
      transition-duration: .25s;
      border-radius: 40px;
    }
  }
  .header__image{
    display: none;
  }
}
@media (min-width:@tablet-width) {
  .header{
    padding-top: 1px;

    .header__logo{
      width: 100px;
      margin-top: 39px;
      margin-left: 5%;
      font-size: 17px;
      padding: 0;
      border: none;

      .header__logo--link{
        border: 5.5px solid #170f1c;
        text-decoration: none;
        color: inherit;
        padding: 3px 9px 3px 8px;
        transition: all linear 0.25s;

        &:hover{
          background-color: rgba(118,244,234,.5);
          border: 5.5px solid black;
          transition-duration: 0.25s;
          border-radius: 40px;
        }
      }

      .header__logo--symbol{
        font-size: 22px;
      }
    }
    .header__intro{
      font-size: 28px;
    }
    .header__button{
      width: 25%;
      margin: 50px 37.5% 100px;
    }
  }
}
@media (min-width:@desktop-width) {
  .header{

    .header__logo{
      margin-top: 60px;
      margin-left: 5%;
      border: none;
    }
    .header__logo--link{
      border: 5.5px solid #170f1c;
      text-decoration: none;
      color: inherit;
      padding: 2px 9px 2px 8px;
      transition: all linear 0.25s;

      &:hover{
        background-color: rgba(118,244,234,.5);
        border: 5.5px solid black;
        transition-duration: 0.25s;
        border-radius: 40px;
      }
    }

    .header__intro{
      font-size: 40px;
      margin-top: 150px;
    }
    .header__button{
      width: 16%;
      margin: 100px 42% 15px;
          display: inline;
          padding-bottom: 10px;
    }
    .header__image{
      display: block;
      margin-top: -16px;
      padding-bottom: 130px;
      .header__image--cork{
        margin-left: 75%;
        margin-bottom: 64px;
      }
      .header__image--glass{
        margin-left: -76px;
        margin-top: -84px;
        margin-bottom: 3px;
      }
    }
  }
}
@media (min-width:@desktop-two-width) {
  .header{
    .header__logo{
      margin-top: 61px;
      padding-top: 3px;
      margin-left: 150px;
      padding-bottom: 2px;
      border:none;

      .header__logo--symbol{
        font-size: 21px;
      }
      .header__logo--link{
        border: 5.5px solid #170f1c;
        text-decoration: none;
        color: inherit;
        padding: 2px 9px 2px 8px;
        transition: all linear 0.25s;

        &:hover{
          background-color: rgba(118,244,234,.5);
          border: 5.5px solid black;
          transition-duration: 0.25s;
          border-radius: 40px;
        }
      }
    }
    .header__intro{
      font-size: 45px;
      margin-top: 191px;
      line-height: 1.28;
      letter-spacing: 0px;
      .header__intro--back{
        background-color: rgba(118,244,234,.5);
        padding-left: 7px;
        padding-right: 13px;
        margin-left: -8px;
        display: inline-block;
        margin-right: -5px;
        height: 56px;
      }
      .header__intro--back-two{
        background-color: rgba(118,244,234,.5);
        padding-left: 13px;
        padding-right: 11px;
        display: inline-block;
        margin-right: -7px;
        height: 58px;
      }
      .header__intro--edit{
        margin-left: -4px;
      }
    }
    .header__button{
      letter-spacing: 2.16px;
      line-height: 1.26;
      font-size: 18px;
      padding: 14px 0;
      font-weight: 600;
      margin-top: 109px;
    }
    .header__image{
      padding-bottom: 60px;
    }
  }
}
