@import url("variables");

.footer{
  background-color: #262b2e;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  font-family: @Raleway;
  padding-top: 25px;
  padding-bottom: 40px;

  .footer__title{
    line-height: 1.56;
    letter-spacing: 0.16px;
    font-family: @Raleway;
    font-weight: 400;
  }

  .footer__icon{

    .footer__icons{
      display: flex;
      text-align: center;
      justify-content: center;
      margin-left: 20%;
      margin-right: 20%;
      padding-left: 0;

      .footer__item{
        list-style-type: none;
        width: 46px;
        height: 40px;

      }
      .footer__item:nth-child(1),.footer__item:nth-child(2),.footer__item:nth-child(3){
        margin-right: 7px;

      }
      .footer__item--pinterest{
        background-image: url("../../build/img/pinterest.svg");
        background-size: 20px;
        background-color: rgba(138, 138, 138, 0.4);
        background-repeat: no-repeat;
        background-position: center;
        margin-right: 7px;
        display: block;
        width: 46px;
        height: 40px;
        transition: all linear 0.25s;
        &:hover{
          background-color:rgba(113, 246, 235, 1);
          transition-duration: 0.25s;
          background-image: url("../../build/img/pinterest-hover.svg");
        }

      }
      .footer__item--twitter{
        background-image: url("../../build/img/twitter.svg");
        background-size: 20px;
        background-color: rgba(138, 138, 138, 0.4);
        background-repeat: no-repeat;
        background-position: center;
        margin-right: 7px;
        display: block;
        width: 46px;
        height: 40px;
        transition: all linear 0.25s;
        &:hover{
          background-color:rgba(113, 246, 235, 1);
          transition-duration: 0.25s;
          background-image: url("../../build/img/twitter-hover.svg");
        }
      }
      .footer__item--facebook{
        background-image: url("../../build/img/facebook.svg");
        background-size: 20px;
        background-color: rgba(138, 138, 138, 0.4);
        background-repeat: no-repeat;
        background-position: center;
        margin-right: 7px;
        display: block;
        width: 46px;
        height: 40px;
        transition: all linear 0.25s;
        &:hover{
          background-color:rgba(113, 246, 235, 1);
          transition-duration: 0.25s;
          background-image: url("../../build/img/facebook-hover.svg");
        }
      }
      .footer__item--basketball{
        background-image: url("../../build/img/basketball.svg");
        background-size: 20px;
        background-color: rgba(138, 138, 138, 0.4);
        background-repeat: no-repeat;
        background-position: center;
        display: block;
        width: 46px;
        height: 40px;
        transition: all linear 0.25s;
        &:hover{
          background-color:rgba(113, 246, 235, 1);
          transition-duration: 0.25s;
          background-image: url("../../build/img/basketball-hover.svg");
        }
      }
    }
  }
}
@media (min-width:768px) {
  .footer{
    padding-bottom: 70px;

    .footer__title{
      font-size: 16px;
    }
  }
}
@media(min-width:1200px){
  .footer{
    padding-bottom: 130px;
    .footer__title{
      margin-top: 62px;
      margin-bottom: -4px;
    }
  }
}
@media (min-width:1440px) {
  .footer{
    padding-bottom: 140px;
    .footer__title{
      margin-top: 63px;
      margin-bottom: -4px;
      margin-left: -4px;
    }
    .footer__icon{
      margin-left: -4px;
    }
  }
}
