@import url("variables");

.works{
  margin-top: 70px;

  .works__title{
    font-family: @Montserrat;
    font-size: 23px;
    color: rgba(0, 0, 0, 0.9);
    letter-spacing: 1.28px;
  }
  .works__text{
    font-family:@Merriweather;
    color: rgba(0, 0, 0, 0.62);
    font-size: 14px;
    margin-bottom: 50px;
    margin-left: 5%;
    margin-right: 5%;
  }
  .works__flex{
    display: flex;
    flex-direction: column;
    margin-top: 20px;

    .works__photo{
      width: 90%;
      margin-top: 5%;
      margin-left: 5%;
      margin-right: 5%;

    }
  }
}
@media (min-width:@tablet-width) {
  .works{

    .works__title{
      font-size: 28px;
      margin-bottom: 0;
      margin-top: 100px;
    }
    .works__text{
      margin-top: 5px;
    }
    .works__flex{
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;

      .works__photo{
        width: 30%;
        margin: 1% 1%;
      }
    }
  }
}
@media (min-width:@desktop-width) {
  .works{

    .works__title{
      font-size: 33px;
      padding-top: 70px;
    }
    .works__text{
      font-size:15px;
      margin-bottom: 80px;
    }
    .works__flex{

      .works__photo{
        width: 25%;
        margin: 7px;
      }
    }
  }
}
@media (min-width:@desktop-two-width) {
  .works{
    margin-top: 184px;

    .works__title{
      font-size: 38px;
      margin-left: 40px;
      letter-spacing: 0.6px;
      font-weight: 800;
    }
    .works__text{
      font-size: 17px;
      letter-spacing: 1.2px;
      line-height: 0.9;
    }
    .works__flex{
      margin-top: 114px;

      .works__photo{
        width: 26%;
        margin: 5px;
      }
    }
  }
}
